}

.slider {
  width: 100%;
}

.slider__item {
  background-size: cover;
  background-position: center;
  position: relative;
}

.slider__item::after {
  content: '';
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(270deg, #1C222A 0%, rgba(71, 71, 71, 0.7) 48.62%, rgba(255, 255, 255, 0.3) 100%);
  transform: rotate(-180deg);
}

.slider__content {
  padding: 93px 20px 130px 60px;
  position: relative;
  z-index: 2;
}

.slider__title {
  font-size: 64px;
  line-height: 75px;
  letter-spacing: .02em;
  color: #fff;
}

.slider__desc {
  font-size: 24px;
  line-height: 28px;
  letter-spacing: .02em;
  color: #fff;
}

.slider__info {
  margin: 36px 0;
}

.slider__info__block {
  margin: 9px 0;
  line-height: 21px;
}

.slider__info__block__icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 20px;
}

.slider__info__block__text {
  font-size: 18px;
  letter-spacing: .02em;
  color: #fff;
}

.slider .owl-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1140px;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.slider .owl-prev {
  margin-left: -20px;
  color: #fff;
}

.slider .owl-next {
  margin-right: -20px;
  color: #fff;
}

.slider .btn--yellow {
  padding: 16px 50px;
}

.social {
  position: absolute;
  right: 10px;
  bottom: 30px;
  display: flex;
  flex-direction: column;
  z-index: 2;
}

.social__item {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 26px;
  height: 26px;
  margin: 5px 0;
  border: 2px solid #fff;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
}

.social__item .fab {
  font-size: 12px;
  color: #fff;
}

.old-site {
