}

.posters__main {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  flex: 6;
  margin-top: 50px;
}

.poster {
  width: 210px;
  margin: 8px 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.25);
  line-height: 0;
  position: relative;
  transition: ease .3s;
  overflow: hidden;
}

.poster:hover {
  box-shadow: 0x 4px 20px rgba(0, 0, 0, 0.7);
}

.poster:hover .poster__details {
  opacity: 1;
  transform: translateY(0);
}

.poster__details {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding: 15px 15px 45px 15px;
  font-size: 14px;
  line-height: 16px;
  color: #000;
  text-align: center;
  background: linear-gradient(360deg, #FFB100 0%, rgba(196, 196, 196, 0.7) 100%);
  opacity: 0;
  transform: translateY(100%);
  transition: ease .3s;
}

.poster__details .btn {
  margin-top: 8px;
